export default {
	data() {
		return {

		}
	},
	methods: {
		edit(){
			uni.showActionSheet({
				itemList:['复制','编辑','删除'],
				success(res) {
					if(res.tapIndex==0){
						uni.showToast({
							title:'复制成功!',
							icon:"none"
						})
					}
					if(res.tapIndex==1){
						uni.showToast({
							title:'编辑成功!',
							icon:"none"
						})
					}
					if(res.tapIndex==2){
						uni.showToast({
							title:'删除成功!',
							icon:"none"
						})
					}
				}
			})
		}
	}
}
